import React, { useEffect , useState , useRef} from 'react';
import styled from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faStickyNote } from '@fortawesome/free-solid-svg-icons'
import { faCalendarAlt } from '@fortawesome/free-solid-svg-icons'
import { faCog } from '@fortawesome/free-solid-svg-icons'

const EditBtn = styled.button`
    border: none; 

    border-radius: 0; 
    height: 36px; 
    width: 36px; 
    margin-right: 3px;
    background-color: inherit;
    &:hover {
        cursor: pointer;
        background-color: #d0ebff;
      }
`;
const DelBtn = styled.button`
    border: none; 
    border-radius: 0; 
    height: 36px; 
    width: 36px; 
    background-color: inherit;
    &:hover {
        cursor: pointer;
        background-color: #d0ebff;
      }
`;
const EditInput = styled.input`
flex: 1; 
border: none; 
border-bottom: 1px solid #f1f3f5; 
padding: 5px; 
font-size: 1em; 
box-sizing: border-box;
`;

const PfItem = ({pfItem,pfList,setPfList}) => {
    const editInputRef = useRef(null);

    const [edited,setEdited] = useState(false);
    const [newText,setnewText] = useState(pfItem.title);//ì²˜ìŒì—ëŠ” ê¸°ì¡´ì˜ ê°’ì„ , ì œëª©ë§Œ ìˆ˜ì •ê°€ëŠ¥..

    const onClickDelete = () => {
        if(window.confirm('í•´ë‹¹ í¬íŠ¸í´ë¦¬ì˜¤ë¥¼ ì§€ìš°ê² ìŠµë‹ˆê¹Œ?'))
        {
            const nextpfList = pfList.map((item)=>(
                {
                    ...item,
                    deleted : item.id === pfItem.id ? true : item.deleted
                }
            ));
            setPfList(nextpfList);
        }
    }

    //ìˆ˜ì •ë²„íŠ¼ìœ¼ë¡œ ë³€ê²½í•˜ê¸°
    const onClickEditButton = () => 
    {
        setEdited(true);
    }

    //ìƒˆë¡œìš´ ê¸€ì ì„¸íŒ…
    const onChangeEdit = (e) => {
        setnewText(e.target.value)
    }

    //ì„œë¸Œë°‹ì‹œí‚¤ê¸°
    function onClickSubmitButton (){
        const nextpfList = pfList.map((item)=>(
            {
                ...item,
                title : item.id === pfItem.id ? newText : item.title
            }
        ));
        setPfList(nextpfList); //ìƒˆë¡­ê²Œ ë¦¬ìŠ¤íŠ¸ ìˆ˜ì •

        setEdited(false); // ìˆ˜ì •ìƒíƒœì—ì„œ ë‹¤ì‹œ ì½ê¸°ë¡œ ë³€ê²½
    }

    useEffect(()=> {
        if (edited)
        {
            editInputRef.current.focus();
        }
    },[edited]);
    
    const sticky_note = <FontAwesomeIcon icon={faStickyNote} />
    const calendar_alt = <FontAwesomeIcon icon={faCalendarAlt} />
    const fa_cog = <FontAwesomeIcon icon={faCog} />

    return (
        <div class="portfolio-item">
                <div>
                    <h1 class="portfolio-title">
                        {
                            edited ? 
                            <EditInput
                                type="text"
                                value={newText}
                                ref={editInputRef}
                                onChange={onChangeEdit}
                            /> :
                            pfItem.title
                         }
                    </h1>
                    <div class="portfolio-line"></div>

                    <div class="portfolio-content"> 
                        <span class="portfolio-icon">
                            {sticky_note} <span class="portfolio-icon-text">{pfItem.project}</span>
                        </span>
                        <br/>
                        <span class="portfolio-icon">
                            {calendar_alt} <span class="portfolio-icon-text">{pfItem.date}</span>
                        </span>
                        <br/>
                        <span class="portfolio-icon">
                            {fa_cog} <span class="portfolio-icon-text">{pfItem.skill}</span>
                        </span>
                    </div>
                </div>
            { 
                edited ? 
                (<EditBtn type="button" onClick={onClickSubmitButton} > ğŸ‘Œ </EditBtn>) :
                (<EditBtn type="button" onClick={onClickEditButton}> âœ </EditBtn>)
            }
            <DelBtn type="button" onClick={onClickDelete}> ğŸ—‘ </DelBtn>
        </div>
    );
}

export default PfItem;